import React, { useState, useEffect, useRef } from "react";
import { Helmet } from "react-helmet";
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";
import { useLocation } from "react-router-dom";
import { API_URL } from "../../constants";

import { login, logout } from "../../redux/loginSlice";

import LoginFuncSelect from "./LoginFuncSelect";
import Login from "./Login";
import Register from "./Register";
import Logout from "./Logout";
import LoginWarning from "./Warning/LoginWarning";

import classes from "./LoginPage.module.css";

const LoginPage = (props) => {
  /*  */
  const dispatch = useDispatch();
  const isUserLoggedIn = useSelector((state) => state.login.isUserLoggedIn);
  const userInfo = useSelector((state) => state.login.userInfo);

  /* or */
  const [funcSelect, setFuncSelect] = useState("Login"); // 
  /*  */
  const [warning, setWarning] = useState(false); // 
  const [warningContent, setWarningContent] = useState({}); // 

  /* / UI  */
  const funcChangeHandler = (selected) => {
    // Login || Register
    setFuncSelect(selected);
  };

  /*  */
  const userLoginHandler = async (username, password) => {
    try {
      const response = await axios.post(
        API_URL + "/login/login",
        {
          username,
          password,
        },
        {
          withCredentials: true,
          //  session ID cookie Passport
        }
      );
      alert(response.data.message);
      dispatch(login(response.data.user));
    } catch (err) {
      console.error(" login:", err.response.data);
      alert(err.response.data.message);
    }
  };

  /*  */
  const logoutHandler = async () =>
    await axios
      .get(API_URL + "/login/logout", { withCredentials: true })
      .then((response) => {
        if (response.data.message === "") {
          dispatch(logout());
          alert(response.data.message);
        }
      })
      .catch((err) => {
        console.error(":", err.response.data);
      });
  {
  }

  /*  */
  const userRegisterHandler = async ({
    username,
    password,
    email,
    phone,
    birthday,
  }) => {
    if (username && password && email && phone && birthday) {
      try {
        const response = await axios.post(API_URL + "/login/register", {
          username,
          password,
          email,
          phone,
          birthday,
        });
        alert(response.data.message);
        setFuncSelect("Login");
      } catch (err) {
        console.error(" register:", err.response.data);
        alert(err.response.data.message);
      }
    } else {
      alert("");
    }
  };

  /*  */
  const location = useLocation();
  const notLoginWarning = location.state;
  useEffect(() => {
    if (notLoginWarning) {
      setWarning(notLoginWarning.openWarning); // 
      setWarningContent(notLoginWarning.text); // 
    }
  }, [notLoginWarning]);

  /*  */
  const warningCloseHandler = () => {
    setWarning(false);
    setWarningContent({});
  };

  return (
    <div className={classes.loginPage}>
      <Helmet>
        <title>TheGameC - </title>
      </Helmet>
      {
        warning ? (
          <LoginWarning
            onDialogClose={warningCloseHandler}
            warning={warningContent}
          />
        ) : null /*  */
      }
      <LoginFuncSelect
        funcSelect={funcSelect}
        onChangeFunc={funcChangeHandler}
      />
      {isUserLoggedIn ? (
        <Logout onLogout={logoutHandler} userInfo={userInfo} />
      ) : funcSelect === "Login" ? (
        <Login onLogin={userLoginHandler} />
      ) : (
        <Register onRegister={userRegisterHandler} />
      )}
    </div>
  );
};

export default LoginPage;
